import axios from "axios";
import env from "./env";

axios.defaults.baseURL = env.baseUrl;
axios.defaults.timeout = 3000;
axios.defaults.responseType = 'json';//'blob';

// * http request 
axios.interceptors.request.use(
    config => {
        // * tokenhttp headertoken
        // * token
        // @ts-ignore
        if (env.isDevelopment) console.log('http  ', config);
        return config;
    },
    err => {
        if (env.isDevelopment) console.log('http   ', err);
        return Promise.reject(err);
    });

// * http response 
axios.interceptors.response.use(
    response => {
        if (env.isDevelopment) console.log('http  ', response);
        let data = response.data;
        // * 
        if (data.code === 0) {
            // * data
            return response;
        }
        // * code20103 token()
        // * login
        if (data.code === 20103) {
            //router.replace('/login');
            if (env.isDevelopment) console.log('token ');
        }
        //return Promise.reject(data);
        return response;
    },
    error => {
        if (env.isDevelopment) console.log('http   ', error);
        return Promise.reject(error);
    });


export default axios;